












import Vue from 'vue'
import {  Component, Prop } from 'vue-property-decorator'
@Component

export default class Types extends Vue {
  @Prop(String) classPrefix?: string
  
    @Prop(String)  value!: string 
  
  
 
  ctype(e: string){
    console.log(e);
    
          if(e!=='-'&&e!=='+'){
            throw new Error('type is unknown')
          }
          this.$emit('update:value',e)
        }
  
}
